<template lang="html">
  <div class="">
    <con-doc  :apix="api" titlex="Notifications" textx="Notificaciones agradables simples e interactivas." >
      <div slot="codes">
        <codex :float="true" titlex="Notifications Defaults" :code="code1">
          <div slot="ejemplo">

            <vs-button @click="vsNoti('Lorem ipsum dolor sit amet, consectetur','white')" vs-type="primary-border">Notificacion white</vs-button>

            <vs-button @click="vsNoti('Lorem ipsum dolor sit amet, consectetur','primary')" vs-type="primary-flat">Notificacion Primary</vs-button>

            <vs-button @click="vsNoti('Lorem ipsum dolor sit amet, consectetur','success')" vs-type="success-flat">Notificacion Success</vs-button>

            <vs-button @click="vsNoti('Lorem ipsum dolor sit amet, consectetur','danger')" vs-type="danger-flat">Notificacion Danger</vs-button>

            <vs-button @click="vsNoti('Lorem ipsum dolor sit amet, consectetur','warning')" vs-type="warning-flat">Notificacion Warning</vs-button>

            <vs-button @click="vsNoti('Lorem ipsum dolor sit amet, consectetur','dark')" vs-type="dark-flat">Notificacion Dark</vs-button>

            <vs-button @click="notiAzar()" vs-type="success-gradient">Notificacion Color Al Azar</vs-button>

          </div>
          </codex>

          <codex :float="true" titlex="Positions" :code="code2">
            <div slot="ejemplo">
              <vs-button @click="vsNoti('Lorem ipsum dolor sit amet, consectetur','primary')" vs-type="primary-flat">Position Default</vs-button>

              <vs-button @click="vsNoti('Lorem ipsum dolor sit amet, consectetur','success','top-right')" vs-type="success-flat">Position Top Right</vs-button>

              <vs-button @click="vsNoti('Lorem ipsum dolor sit amet, consectetur','danger','top-left')" vs-type="danger-flat">Position Top Left</vs-button>

              <vs-button @click="vsNoti('Lorem ipsum dolor sit amet, consectetur','warning','bottom-left')" vs-type="warning-flat">Position Bottom Left</vs-button>

              <vs-button @click="vsNoti('Lorem ipsum dolor sit amet, consectetur','dark','bottom-center')" vs-type="dark-flat"> Position Bottom Center</vs-button>

              <vs-button @click="notiAzarCenter()" vs-type="success-gradient">Position Top Center Color Al Azar</vs-button>
            </div>
            </codex>

            <codex :float="true" titlex="Icons" :code="code3">
              <div slot="ejemplo">

                <p class="px">Los iconos de Vuesax utilizan la biblioteca de fuentes de Google Material Icons. Para obtener una lista de todos los íconos disponibles, visite la página oficial de <a href="https://material.io/icons/">Iconos de materiales</a>. </p>
                <vs-button @click="vsNoti('Lorem ipsum dolor sit amet, consectetur','primary',null,'mail')" vs-type="primary-flat">Icon Mail</vs-button>

                <vs-button @click="vsNoti('Lorem ipsum dolor sit amet, consectetur','success',null,'check_box')" vs-type="success-flat">Icon check_box</vs-button>

                <vs-button @click="vsNoti('Lorem ipsum dolor sit amet, consectetur','danger',null,'favorite')" vs-type="danger-flat">Icon favorite</vs-button>

                <vs-button @click="vsNoti('Lorem ipsum dolor sit amet, consectetur','warning',null,'error')" vs-type="warning-flat">Icon error</vs-button>

                <vs-button @click="vsNoti('Lorem ipsum dolor sit amet, consectetur','dark',null,'chat')" vs-type="dark-flat">Icon chat</vs-button>

                <vs-button @click="notiAzarIcon()" vs-type="success-gradient">Icon Color Al Azar</vs-button>
              </div>
              </codex>

              <codex :float="true" titlex="Fixed and function click" :code="code4">
                <div slot="ejemplo">
                  <vs-button @click="myFunction()" vs-type="primary-flat">Run Example</vs-button>
                </div>
              </codex>

      </div>
    </con-doc>
  </div>
</template>

<script>
import conDoc from '../../components/conDoc.vue'
import titlex from '../../components/title.vue'
import codex from '../../components/code.vue'
import $ from 'jquery'
export default {
  components:{
    conDoc,
    codex,
  },
  data(){
    return{
      api:[
        {
          name:'vsNoti',
          type:'function',
          parametros:'(text,type||color,position,icon,function,fixed)',
          descripcion:'Funcion que hace aparecer la notificacion',
          default:'null',
          ejemplo:"vsNoti('Lorem ipsum dolor sit amet, consectetur','primary','top-left','chat')"
        },
      ],
      code1:`<vs-button
  @click="vsNoti('Lorem ipsum dolor sit amet, consectetur','white')"
  vs-type="primary-border">Notificacion white</vs-button>

<vs-button
  @click="vsNoti('Lorem ipsum dolor sit amet, consectetur','primary')"
  vs-type="primary-filled">Notificacion Primary</vs-button>

<vs-button
  @click="vsNoti('Lorem ipsum dolor sit amet, consectetur','success')"
  vs-type="success-filled">Notificacion Success</vs-button>

<vs-button
  @click="vsNoti('Lorem ipsum dolor sit amet, consectetur','danger')"
  vs-type="danger-filled">Notificacion Danger</vs-button>

<vs-button
  @click="vsNoti('Lorem ipsum dolor sit amet, consectetur','warning')"
  vs-type="warning-filled">Notificacion Warning</vs-button>

<vs-button
  @click="vsNoti('Lorem ipsum dolor sit amet, consectetur','dark')"
  vs-type="dark-filled">Notificacion Dark</vs-button>
`,
      code2:`<vs-button
 @click="vsNoti('Lorem ipsum dolor sit amet, consectetur','primary')"
 vs-type="primary-flat">Position Default</vs-button>

<vs-button
 @click="vsNoti('Lorem ipsum dolor sit amet, consectetur','success','top-right')"
 vs-type="success-flat">Position Top Right</vs-button>

<vs-button
 @click="vsNoti('Lorem ipsum dolor sit amet, consectetur','danger','top-left')"
 vs-type="danger-flat">Position Top Left</vs-button>

<vs-button
 @click="vsNoti('Lorem ipsum dolor sit amet, consectetur','warning','bottom-left')"
 vs-type="warning-flat">Position Bottom Left</vs-button>

<vs-button
 @click="vsNoti('Lorem ipsum dolor sit amet, consectetur','dark','bottom-center')"
 vs-type="dark-flat"> Position Bottom Center</vs-button>`,

 code3:`<vs-button
  @click="vsNoti('Lorem ipsum dolor sit amet, consectetur','primary',null,'mail')"
  vs-type="primary-flat">Icon Mail</vs-button>

 <vs-button
  @click="vsNoti('Lorem ipsum dolor sit amet, consectetur','success',null,'check_box')"
  vs-type="success-flat">Icon check_box</vs-button>

 <vs-button
  @click="vsNoti('Lorem ipsum dolor sit amet, consectetur','danger',null,'favorite')"
  vs-type="danger-flat">Icon favorite</vs-button>

 <vs-button
  @click="vsNoti('Lorem ipsum dolor sit amet, consectetur','warning',null,'error')"
  vs-type="warning-flat">Icon error</vs-button>

 <vs-button
  @click="vsNoti('Lorem ipsum dolor sit amet, consectetur','dark',null,'chat')"
  vs-type="dark-flat">Icon chat</vs-button>`,

  code4:`#template
  <vs-button @click="myFunction()" vs-type="primary-flat">Run Example</vs-button>

#methods
 methods:{
  myFunction(){
    #javascript vanilla
     let _this = this
     this.vsNoti('Has click en mi',null,'top-center','mail',function(){
        _this.vsNoti('function segundaria ejecutada','success','top-center','check_box',null,false)
     },false)
   }

   #ECMAScript 6
   this.vsNoti('Has click en mi','primary','top-center','mail',()=>{
       this.vsNoti('function segundaria ejecutada','success','top-center','check_box',null,true)
   },true)
 }

  `

}
  },
  methods:{
    pruebax(){
      console.log('hola mundo vuesax')
    },
    myFunction(){
      this.vsNoti('Has click en mi','primary','top-center','mail',()=>{
          this.vsNoti('function segundaria ejecutada','success','top-center','check_box',null,true)
      },true)
    },
    notiAzar(){
      function getRandomInt(min, max) {
        return Math.floor(Math.random() * (max - min)) + min;
      }
let color = `rgb(${getRandomInt(0,255)},${getRandomInt(0,255)},${getRandomInt(0,255)})`

      this.vsNoti('Lorem ipsum dolor sit amet, consectetur',color)

    },
    notiAzarIcon(){
      function getRandomInt(min, max) {
        return Math.floor(Math.random() * (max - min)) + min;
      }
let color = `rgb(${getRandomInt(0,255)},${getRandomInt(0,255)},${getRandomInt(0,255)})`

      this.vsNoti('Lorem ipsum dolor sit amet, consectetur',color,null,'verified_user')

    },
    notiAzarCenter(){
      function getRandomInt(min, max) {
        return Math.floor(Math.random() * (max - min)) + min;
      }
let color = `rgb(${getRandomInt(0,255)},${getRandomInt(0,255)},${getRandomInt(0,255)})`

      this.vsNoti('Lorem ipsum dolor sit amet, consectetur',color,'top-center')

    }
  }
}
</script>

<style lang="css" scoped>
.px {
  padding: 10px;
  font-size: 15px;
  padding-bottom: 30px;
  color: rgba(0, 0, 0, 0.7);
}
</style>



